// unstyled list

.unstyled {
  margin-left: 0;
  list-style: none;
  li {margin: 0.5rem 0;}
}

// spaced list

.spaced {
  li {margin: 0.5rem 0;}
}

// disc list

.disc {
  margin-left: 1.25rem;
  list-style: disc;

  ul {
    margin-left: 1.25rem;
    list-style: circle;
  }
}

// decimal list

.decimal {
  margin-left: 1.25rem;
  list-style: decimal;
}

// lower alpha list

.lower-alpha li {
  margin-left: 1.25rem;
  list-style: lower-alpha;

  ol {margin-bottom: 0;}
}

// checked list

ul.checked {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      position: absolute;
      left: 0; top: 0.3rem;

      @-moz-document url-prefix() {top: 0.2rem;}

      .services & {color: $clr-b2;}
      .resources & {color: $clr-b3;}
      .associates & {color: $clr-b4;}
      .toolkit & {color: $clr-b6;}
      .bg-dark & {color: white;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// asterisk list

ul.asterisk {
  margin-left: 0!important;
  list-style: none!important;

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.3rem;

      @-moz-document url-prefix() {top: 0.2rem;}

      body.services & {color: $clr-b2;}
      body.academy &,
      body.associates &,
      ol.events & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }

    blockquote,
    blockquote p {margin-bottom: 0;}
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// chevroned list

ul.chevroned {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1rem;

    &:before {
      @extend .icon-extension,.icon-angle-right;
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-16;

      body.services & {color: $clr-b2;}
      body.academy & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }
  }

  &.plain a {
    text-decoration: none;

    @include bp-m {
      &:hover,
      &:focus {text-decoration: underline;}
    }
  }
}

// icon list

ul.icons {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      line-height: 1.4rem;
      font-size: $fs-20;

      body.services & {color: $clr-b2;}
      body.academy & {color: $clr-b4;}
      body.resources & {color: $clr-b3;}
    }
  }
}

// styled list

ul.styled {
  margin-bottom: 0;
  border-radius: $border-radius;
  padding: ($spacing*0.5) $spacing;
  background: rgba($clr-2,0.05);

  @include bp-m {font-size: $fs-22;}

  .bg-dark & {
    background: rgba(black,0.2);
    li:before {color: white!important;}
  }

  li {margin: 0.5rem 0!important;}
}

// buttons

ul.buttons {
  text-align: center;

  &.inline {
    @include bp-m {
      display: flex;
      justify-content: center;
    }
  }

  div.service-process div.text & {margin-bottom: 0;}

  li {
    display: inline-block;
    margin: 0.5rem 0;
    
    @include bp-m {
      flex: 0 1 auto;
      margin: 0.5rem 0.25rem;
    }
  }
}

// questions

ul.questions {
  @include lead();

  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.4em;

    &:before {
      @extend .icon-extension,.icon-asterisk;
      position: absolute;
      left: 0; top: 0.2rem;
    }
  }
}

// hierarchy links

div.hierarchy {
  padding: 0.4rem 0;
  background: $clr-3;

  ol {
    display: flex;
    align-items: baseline;
    margin: 0;
    font-size: $fs-16;

    li {
      margin-right: 0.35rem;

      &:before {
        @extend .icon-extension, .icon-angle-left;
        flex: 0 1 auto;
        margin-right: 0.25rem;
        font-size: $fs-10;
      }
    
      a,
      > span {
        flex: 0 1 auto;
        text-decoration: none;
        color: #3B3D41;
      }
      
      a {
        @include bp-m {
          &:hover,
          &:focus {
            text-decoration: underline;
            color: $clr-b1;
          }
        }
      }
    }
  }
}

// share options

ul.share-options {
  margin-bottom: 0;
  font-size: $fs-20;

  li {
    display: inline-block;
    margin-right: 0.25rem;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3rem;
      width: 1.7rem;
      height: 1.7rem;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      &.icon-linkedin {background: #0077B5;}
      &.icon-twitter {background: #55ADEE;}
      &.icon-facebook {background: #4267B2;}
      &.icon-envelope {background: #7D7D7D;}

      @include bp-m {
        &:hover,
        &:focus {box-shadow: inset 0 0 0 4rem rgba(black,0.1);}
      }

      span {@include hide-screen();}
    }

    &:last-child {margin-right: 0;}
  }
}

// brand assets

ul.brand-assets {
  border-top: 1px solid $clr-3;

  li {
    border-bottom: 1px solid $clr-3;
    padding: 0.4rem 0;

    a {
      text-decoration: none;
      @include bp-m {
        &:hover,
        &:focus {text-decoration: underline;}
      }
    }
  }
}

// videos

ol.videos li.col {flex: 0 1 auto;}

// document list

ul.documents {
  li {
    position: relative;
    margin-bottom: 0.25rem;
    padding-left: 1.5rem;

    &:before {
      @extend .icon-extension,.icon-file;
      position: absolute;
      left: 0; top: 1px;
      font-size: $fs-24;
    }
  }
}

// numbered discs

ol.numbered-discs {
  margin-left: 0!important;
  list-style: none!important;
  counter-reset: numbered-discs;

  li {
    position: relative;
    margin-bottom: 0.5rem;
    padding-left: 2rem;

    &:before {
      position: absolute;
      left: 0; top: 0;
      margin-right: 1rem;
      border-radius: 50%;
      padding: 0;
      width: 1.3rem;
      height: 1.3rem;
      line-height: 1.4rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 700;
      font-size: $fs-16;
      color: white;
      counter-increment: numbered-discs;
      content: counter(numbered-discs);

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
      body.academy & {background: $clr-b4;}
    }
  }
}

// numbered blocks

ol.numbered-blocks {
  counter-reset: numbered-blocks;
  text-align: center;

  @include bp-m {text-align: left;}

  &.alt.alt li:before {
    @extend .icon-extension,.icon-check;
    background: $clr-b1;
  }

  li {
    position: relative;
    margin-bottom: 4px;
    padding: ($spacing*1.25) $spacing;
    background: $clr-4;

    &:first-child {border-radius: $border-radius $border-radius 0 0;}
    &:last-child {border-radius: 0 0 $border-radius $border-radius;}

    @include bp-m {padding-left: $spacing*1.75;}

    &:before {
      position: absolute;
      right: 50%; top: 0;
      margin: -1.3rem -1.25rem 0 0;
      border-radius: 50%;
      border: 4px solid white;
      padding-top: 0.35rem;
      width: 2.5rem;
      height: 2.5rem;
      background: $clr-b1;
      text-align: center;
      font-weight: 400;
      font-size: $fs-20;
      color: white;
      counter-increment: numbered-blocks;
      content: counter(numbered-blocks);
      box-sizing: border-box;

      @-moz-document url-prefix() {padding-top: 0.4rem;}

      body.services & {background: $clr-b2;}
      body.resources & {background: $clr-b3;}
      body.associates & {background: $clr-b4;}
      body.toolkit & {background: $clr-b6;}

      @include bp-m {
        right: 100%; top: 50%;
      }
    }

    > :last-child {margin-bottom: 0;}
  }
}

// information



  ul.row.prices {
    @include bp-m {
      justify-content: center;
      li:first-child div.content {border-radius: $border-radius 0 0 $border-radius;}
      li:last-child div.content {border-top-right-radius: $border-radius; border-bottom-right-radius: $border-radius;}
    }

    li.col {
      max-width: $col-5;
      div.content {overflow: hidden;}
    }
  }


ul.information,
ol.information {
  text-align: left;

  &.compact {
    div.content p {
      padding: ($spacing*0.75);
      font-size: $fs-20;

