  &.cross-sell {
    div.content {

      &.bg-dark h3 {background: rgba($clr-1,0.2);}
      &.bg-b5 h3 {background: rgba($clr-1,0.075);}

      @include bp-m {
        display: flex;
        flex-direction: column;
      }

      h3 {
        @extend .h4;
        flex: 0 1 auto;
        margin: 0;
        padding: ($spacing*0.75) ($spacing/1.5);
        text-align: center;

        span.type {
          display: block;
          font-size: $fs-20;
          font-weight: 700;
        }
      }

      div.text {
        font-size: $fs-17;
      }

      div.cta {
        font-size: $fs-18;
        
        // p.prices {
        //   margin-bottom: ($vmargin/2);

        //   span.price {
        //     display: block;
        //     font-size: $fs-40;
        //     line-height: 1.1;
        //   }

        //   span.info {font-size: $fs-18;}
        //   span.vat {font-size: $fs-14;}
        // }
      }
    }
  }

  &.prices {
    div.content {

      &.bg-dark h3 {background: rgba($clr-1,0.2);}
      &.bg-b5 h3 {background: rgba($clr-1,0.075);}

      @include bp-m {
        display: flex;
        flex-direction: column;
      }

      h3 {
        @extend .h3;
        flex: 0 1 auto;
        margin: 0;
        padding: ($spacing*0.75) ($spacing/1.5);
        text-align: center;

        span {
          display: block;
          font-size: $fs-20;
          font-weight: 700;
        }
      }

      div.text {
        font-size: $fs-18;
      }

      div.cta {
        font-size: $fs-18;
        
        p.prices {
          margin-bottom: ($vmargin/2);

          span.price {
            display: block;
            font-size: $fs-40;
            line-height: 1.1;
          }

          span.info {font-size: $fs-18;}
