  div.content {
    flex: 1;
    &:not([class*="bg-dark"],[class*="bg-b5"]) {background: $clr-4;}
  
    img {
      display: block;
      width: 100%;
    }

    h2,h3 {
      @extend .h2;
      margin: 0;
      padding: ($spacing*0.75) ($spacing/1.5);
      background: $clr-3;
      text-align: center;
