  // team

  &.team {

    > li.col {
      display: block;

      div.content {
        padding: ($spacing*1.25);

        @include bp-m {
          padding: ($spacing*1.75);

          > div.row > div.col-8 {
            display: flex;
            align-items: center;
          }
