        h4 {
          margin: 0;
          text-align: center;

          a.author-linkedin {font-size: $fs-70;}

          span {
            &.name {
              @extend .h3;
            }

            &.title {
              display: block;
              margin-top: 0.25rem;
              font-weight: 400;
              font-size: $fs-20;
              @include bp-m-less {margin-bottom: $vmargin;}
            }
          }
        }
          
        p {
          padding: 0;
          
          a.button.linkedin {
            margin-top: 0.75rem;
            border: 0;
            border-radius: 3rem;
            box-shadow: none;

            &:before {
              margin: 0;
            }

            span {@include hide-screen();}
          }
        }
      }
    }
  }
