// features grid

div.features-grid.general-wrap {

  ul {
    li.col {
      padding: 0;
      box-shadow: -4px 0 0 0 rgba($clr-2,0.075), 0 -4px 0 0 rgba($clr-2,0.075);

      div.content {
        padding: ($spacing) 0;

        @include bp-m {
          flex: 1;
          padding: ($spacing*2);
        }

        &[class*="icon-"] {
          text-align: center;

          &:before {
            display: block;
            margin-bottom: 1rem;
            font-size: $fs-40;
            color: $clr-b1;

            @include bp-m {font-size: $fs-50;}

            body.services & {color: $clr-b2;}
            body.resources & {color: $clr-b3;}
            body.associates & {color: $clr-b4;}
            body.toolkit & {color: $clr-b6;}
          }
        }

        h3 {
          @extend .h2;
          margin-bottom: 1rem;
          span {display: block;}
        }

        > :last-child {margin-bottom: 0;}
      }
    }
  }
}

// contact buttons

ul.contact-buttons {
  li {
    min-width: 0; // hack to make the email address wrap inside a flex child 

    a.content {
      display: flex;
      align-items: center;
      padding: ($spacing/1.25) $spacing;
      min-width: 0; // hack to make the email address wrap inside a flex child 
      background: $clr-4;
      text-align: left;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.15s color 0.15s;

      div.bg-b5 & {background: rgba($clr-2,0.075);}

      div.cta &,
      div.bg-dark & {
        background: rgba(white,0.05);
        color: white;
      }

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;
          color: $clr-b1;
          div.bg-b5 & {background: rgba($clr-2,0.125);}
          div.bg-dark & {background: rgba(white,0.075);}
        }
      }

      &:before {
        margin: 0 0.5rem 0 0;
        font-size: $fs-35;
        color: $clr-b1;
      }

      &[class*="linkedin"]:before {color: #0a66c2;}
      &[class*="youtube"]:before {color: #d70000;}
      &[class*="twitter"]:before {color: #1da1f2;}

      > span {
        @extend .text-wrap;
        min-width: 0; // hack to make the email address wrap inside a flex child 
        
        span {
          @include hide-screen();
        }
      }
    }
  }
}

// contact options

ul.contact-options {
  li {
    position: relative;
    padding-left: 1.5rem;

    &:before {
      position: absolute;
      top: 0.2rem; left: 0;
      font-size: $fs-24;

      footer.footer & {top: 0.05rem;}
    }

    &.email {
      overflow-wrap: break-word;
      word-wrap: break-word;

      span {
        @include bp-s-less {@include hide-screen();}
        footer & {@include hide-screen();}
      }
    }

    &.address {
      div.box &,
      footer & {@include hide-screen();}
      span {display: block;}
    }

    a {
      text-decoration: none;
      
      main & {
        @include bp-m {
          &:hover,
          &:focus {text-decoration: underline;}
        }
      }
    }

    span.icon-angle-right:before {
      display: inline-block;
      vertical-align: middle;
      margin: 0 0.5rem;
      font-size: $fs-10;
    }
  }
}

// call options

div.call-options.general-wrap {

  @include bp-s-less {
    margin-left: -$gutter;
    margin-right: -$gutter;
    border-radius: 0;
  }

  > ul {
    text-align: center;

    input.call-option {
      @include hide-input();

      &:checked {
        ~ label {display: none;}
        ~ div.content {display: flex;}
      }
    }

    > li {
      @include bp-m {min-height: 23rem;}

      label.cta {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: ($spacing*2) $spacing;
        background: $clr-4;
        font-size: $fs-24;
        cursor: pointer;
        transition: background-color 0.15s;

        @include bp-m {font-size: $fs-30;}

        &:before {
          display: block;
          margin-bottom: 1rem;
          font-size: $fs-40;
          color: $clr-b1;

          @include bp-m {font-size: $fs-80;}
        }

        @include bp-m {
          &:hover,
          &:focus {background: $clr-3;}
        }

        b {
          text-decoration: underline;
          text-decoration-thickness: 4px;
          text-underline-offset: 3px;
          text-decoration-color: $clr-b1;
        }
      }

      div.content {
        display: none;
        flex: 1;
        flex-direction: column;
        justify-content: center;
        padding: $spacing;
        background: $clr-1;
        color: white;

        > :last-child {margin-bottom: 0;}

        @include bp-m-less {
          &.call-option-1 {padding: ($spacing*2) $spacing;}
        }

        h2 {
          @extend .h3;

          b {
            text-decoration: underline;
            text-decoration-thickness: 4px;
            text-underline-offset: 3px;
            text-decoration-color: white;
          }
        }

        a {
          color: white;

          @include bp-m {
            &:hover,
            &:focus {text-decoration: none;}
          }
        }

        p.availability {
          font-size: $fs-16;
          span {display: block;}
        }

        p.email {
          font-size: $fs-18;
          span {display: block;}
        }

        p.timezone {
          margin: 0.5rem 0 0;
          font-size: $fs-16;
        }

        div.call-time {
          ol {
            overflow: hidden;
            display: flex;
            margin: 0;
            font-size: $fs-18;

            &.call-hours {border-radius: 0.25rem 0.25rem 0 0;}

            &.call-minutes {
              margin-top: 1px;
              border-radius: 0 0 0.25rem 0.25rem;
            }

            li {
              flex: 1;
              margin-right: 1px;
              background: $clr-2;


              &:last-child {margin-right: 0;}

              input {
                @include hide-input();

                &:checked {
                  ~ label {
                    background: $clr-b1;
                    color: white;
                  }
                }
              }

              label {
                display: block;
                padding: 0.5rem 0.2rem;
                background: rgba(white,0.001);
                cursor: pointer;
                transition: background-color 0.15s;

                @include bp-m {
                  &:hover,
                  &:focus {
                    background: rgba(white,0.05);
                    color: $clr-b1;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

// site map

ul.site-map {
  overflow: hidden;
  border-radius: $border-radius;

  > li {

    a {
      display: block;
      border-bottom: 1px solid white;
      padding: 0.5rem 1rem;
      background: $clr-3;
      text-decoration: none;
      color: $clr-1;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
          box-shadow: inset 0 0 0 100rem rgba($clr-2,0.05);
        }
      }
    }

    ul {
      margin: 0;
      font-size: $fs-18;
      
      li {
        a {
          padding-left: 2rem;
          background: $clr-4;

          em {
            font-weight: 700;
            font-style: normal;
            &:after {content: " - "}
          }
          
          span.snippet b {font-weight: 400;}
        }

        &:last-child a {border-bottom: 0;}
      }
    }
  }
}

// faqs 

ol.faqs {
  margin: 0;
  counter-reset: faq-item;

  li {
    margin: 0 0 4px;
    counter-increment: faq-item;

    &:last-child {margin-bottom: 0;}

    input {
      @include hide-input();

      + h3 {
        @extend .h4;
        margin: 0;
        color: $clr-b0; // important for when in a dark background strip
          
        label {
          position: relative;
          display: block;
          margin: 0;
          padding: $spacing;
          padding-right: 3rem;
          background: $clr-4;
          font-weight: 400;
          color: $clr-1;
          cursor: pointer;
          user-select: none;
          -webkit-user-select: none;

          .bg-b5 & {background: rgba($clr-2,0.075);}
          .bg-dark & {background: rgba(black,0.2); color: white;}

          &:before {
            @extend .icon-extension,.icon-plus-circle;
            position: absolute;
            top: 50%; right: 0;
            z-index: 1;
            margin: -0.5rem 1rem 0 0;
            font-size: 1em;
            color: $clr-b1;

            .bg-dark & {color: white;}
          }

          @include bp-m {
            &:hover {
              background: $clr-3;
              color: $clr-b1;

              .bg-b5 & {background: rgba($clr-2,0.125);}
              .bg-dark & {background: rgba(black,0.3);}
            }
          }

          span:before {
            content: counter(faq-item) ". ";
          }
        }

        + div.answer {
          position: absolute;
          left: -1000em;
          padding: $spacing;
          background: $clr-b5;
          color: $clr-1;
          -webkit-text-size-adjust: 100%; // forcing iOS to stop increasing text size

          div.section.bg-b5 & {background: white;}
          .bg-dark & {background: rgba(white,0.9);}

          :last-child {margin-bottom: 0;}
        }
      }

      &:checked {
        + h3 {

          label {
            background: $clr-2!important; // prevent hover effect
            color: white;

            .bg-dark & {background: rgba(black,0.5)!important;}

            &:before {
              @extend .icon-minus-circle;
              color: white;
            }
          }

          + div.answer {position: static;}
        }
      }
    }
  }
}

