// main funnels

div.general-wrap.core-funnels {

  @include bp-m-less {
    margin: 0 (-$gutter);
    border-radius: 0;
  }

  > ul {

    > li {
      @include bp-m-less {margin: 0;}

      &:nth-child(even) {
        > div.content ul {
          background: rgba(black,0.2);
          li:before {color: white;}
        }
      }

      > div.content {
        padding: ($spacing*1.5) ($spacing);
        @include bp-s {padding: ($spacing*2);}

        h2 {
          @extend .h1;
          @include bp-s-less {text-align: center;}
        }

        &.bg-dark h2 {font-weight: 200;}

        ul {
          margin-bottom: 0;
          border-radius: $border-radius;
          padding: ($spacing*0.5) $spacing;
          background: rgba($clr-2,0.05);

          @include bp-m {font-size: $fs-22;}

          li {margin: 0.5rem 0;}
        }

        div.row {
          > div:last-child > :last-child {margin-bottom: 0;}

          @include bp-m {
            > div:first-child {order: 2;}
          }            
        }

        div.col > :last-child {
          @include bp-m {margin-bottom: 0;}
        }
      }
    }
  }
}

// quick funnels

ul.quick-funnels {
  li {
    .content {
      position: relative;
      display: block;
      padding: ($spacing/2) $spacing;
      padding-right: ($spacing*2.5);
      background: $clr-4;
      text-align: left;
      text-decoration: none;
      transition: background-color 0.15s;

      .bg-b5 & {background: rgba($clr-2,0.075);}

      .bg-dark & {
        background: rgba(black,0.15);
        color: white;

        &:before,
        p {color: white;}
      }

      h3 {margin-bottom: 0;}

      p {
        margin-bottom: 0;
        font-size: $fs-18;
        color: $clr-1;
      }
    }

    a.content {
      &:before {
        @extend .icon-extension, .icon-angle-right;
        position: absolute;
        right: 0; top: 50%;
        margin: -0.75rem 1rem 0 0;
        font-size: $fs-35;
        color: $clr-b1;
      }

      @include bp-m {
        &:hover,
        &:focus {
          background: $clr-3;

          .bg-b5 & {background: rgba($clr-2,0.125);}
          .bg-dark & {background: rgba(black,0.25);}
        }
      }
    }
  }
}

// elements - general lists with/out links and icons

.elements.elements {

  li.col {
    .content {
      position: relative;
      display: block;
      padding: ($spacing) 0;
      background: white;
      text-decoration: none;
      transition: background-color 0.15s;

      @include bp-m {
        flex: 1;
        padding: ($spacing*2);
      }

      h2 {
        margin-bottom: 1rem;
        span {display: block;}
      }

      p,ul {color: $clr-1;}

      > :last-child {margin-bottom: 0;}
    }

    a.content {
      padding: $spacing;
      padding-bottom: $spacing*3;
      background: $clr-4;

      .bg-b5 & {background: rgba($clr-2,0.075);}

      &:after {
        position: absolute;
        bottom: 1rem; left: 50%;
        display: inline-block;
        margin: 0 0 0 -4.25rem;
        border-radius: 0.25rem;
        padding: 0.3rem 0;
        width: 8.5rem;
        background: $clr-b1;
        text-align: center;
        font-size: $fs-18;
        color: white;
        content: "Learn more";
        transition: opacity 0.2s;

        @include bp-m {
          bottom: 0; 
          border-radius: 0.25rem 0.25rem 0 0;
          padding: 0.2rem 0 0.1rem;
        }
      }

      @include bp-m {
        padding: $spacing*1.4;
        padding-bottom: $spacing*1.75;

        &:after {opacity: 0;}

        &:hover,
        &:focus {
          background: $clr-3;
          .bg-b5 & {background: rgba($clr-2,0.125);}

          &:after {opacity: 1;}
        }
      }
    }
  }

  &.partner-logos {
    li {
      a.content {
        padding: 0;

        &:after {content: "Visit site";}

        span {
          display: block;
          padding: $spacing;
          background: white;

          img {
            display: block;
            margin: 0 auto;
            width: 100%;
            max-width: 270px;
          }
        }

        div.text {
          padding: $spacing;
          padding-bottom: $spacing*3.25;
          font-size: $fs-18;

          @include bp-m {padding: ($spacing*1.5);}

          > :last-child {margin-bottom: 0;}
        }
      }
    }
  }

  &.downloads {
    > li {
      a.content h2 {
        @extend .h3;
        margin-bottom: $vmargin/2;

        span.info {
          display: flex;
          margin-bottom: 0.5rem;
          font-weight: 500;
          font-size: $fs-16;
          color: $clr-1;

          > span {
            flex: 0 1 auto;
            padding: 0.15rem 0.5rem;

            &:first-child {
              border-top-left-radius: 0.2rem;
              border-bottom-left-radius: 0.2rem;
            }

            &:last-child {
              border-top-right-radius: 0.2rem;
              border-bottom-right-radius: 0.2rem;
            }

            &.value {
              background: $clr-2;
              color: white;
              
              &.premium {background: $clr-b3;}
            }

            &.asset {background: rgba($clr-2,0.1);}
            &.file {background: white;}
          }
        }

        span.heading {display: block;}
      }

      &.free a.content:after {
        margin-left: -4rem;
        width: 8rem;
        content: "Free download";
      }
    }
  }
}

// event details

ul.event-details {
  overflow: hidden;
  border-radius: $border-radius;
  padding: 1rem;
  background: $clr-4;
  font-size: $fs-18;
  color: $clr-1;

  @include bp-s {padding: 0;}
 
  li {
    @include bp-s {
      display: flex;

      b {
        display: inline-block;
        flex: 0 0 auto;
        padding: 0.5rem;
        min-width: 9rem;
        background: $clr-3;
        text-align: right;
      }

      span {
        display: inline-block;
        flex: 1;
        padding: 0.5rem;
        box-shadow: inset 0 -1px rgba(black,0.05);
      }
    }

    &:last-child > * {box-shadow: none;}
  }
}

// articles
// * provides white grid border effect whilst giving the list a background colour 

ol.articles {
  position: relative;
  z-index: 1;

  @include bp-m {background: rgba($clr-4,0.5);}// *

  li.col {
    @include bp-m {box-shadow: 4px 0 0 0 white, 0 4px 0 0 white;}// *

    a {
      display: block;
      background: $clr-4;
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.2s, color 0.2s;

      @include bp-m {
        display: flex;
        flex-direction: column;

        &:hover,
        &:focus {
          background: $clr-3;
          div.text h3 {color: $clr-b1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin-bottom: 0;

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;

        @include bp-m {
          display: flex;
          flex-direction: column;
          flex: 1;
        }

        h3 {
          @extend .h4;
          margin-bottom: $vmargin/2;
          font-weight: 400;
          color: $clr-b0;

          @include bp-m {flex: 0 1 auto;}
        }

        p {
          margin-bottom: 1rem;
          @include bp-m {flex: 1;}
        }

        div.meta {
          font-size: $fs-16;
          @include bp-m {flex: 0 1 auto;}

          img.author {
            overflow: hidden;
            display: inline-block;
            vertical-align: middle;
            margin-right: 0.3rem;
            border-radius: 3rem;
            width: 2.25rem;
            height: 2.25rem;
            box-shadow: 0 0 0 3px $clr-4;
          }

          span {
            display: inline-block;
            vertical-align: middle;
          }

          time {display: block;}
        }
      }
    }
  }
}

// related articles

ul.related-articles {
  li {
    a {
      display: block;
      background: rgba(white,0.75);
      text-decoration: none;
      color: $clr-1;
      transition: background-color 0.2s, color 0.2s;

      @include bp-m {
        display: flex;
        align-items: center;

        &:hover,
        &:focus {
          background: $clr-3;
          div.text h3 {color: $clr-b1;}
        }
      }

      figure {
        position: relative;
        z-index: 1;
        margin: 0;
        
        @include bp-m {width: 40%;}

        &:before { // stops page jump on slow loading of images
          position: relative;
          z-index: 1;
          display: block;
          padding-top: 56.3%;
          background: $clr-3;
          content: "";
        }

        img {
          position: absolute;
          top: 0; left: 0;
          z-index: 2;
        }
      }

      div.text {
        padding: $spacing;

        @include bp-m {
          flex: 1;
          padding: ($spacing/2) $spacing;
        }

        h3 {
          margin-bottom: $vmargin/2;
          font-size: $fs-22;
          font-weight: 400;
          color: $clr-b0;
        }

        p {
          margin-bottom: 0;
          $font-size: $fs-18;

          @include bp-m {position: absolute; left: -1000em;}
          @include bp-l {position: static;}
        }
      }
    }
  }
}
